body {
  color: #ff0;
  background-image: url("clock-bg.944fae7f.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 20px;
  font-size: 28px;
}

.calendar {
  justify-content: space-around;
  display: flex;
}

.date-style {
  display: flex;
}

.digital-clock {
  text-align: center;
  padding-top: 60px;
  font-size: 40px;
}

.clock {
  width: 500px;
  height: 500px;
  background-color: #fff;
  background-image: url("clock.da8c1c00.jpg");
  background-size: cover;
  border-radius: 50%;
  margin-top: 60px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.clock-arrow {
  transform-origin: bottom;
  background-color: #000;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: absolute;
  transform: translateX(-50%);
}

.clock-center {
  width: 30px;
  height: 30px;
  background-color: #000;
  border-radius: 50%;
  position: absolute;
  top: 236px;
  left: 236px;
}

.clock-seconds__arrow {
  width: 5px;
  height: 40%;
  background-color: red;
  bottom: 50%;
  left: 50%;
}

.clock-minutes__arrow {
  width: 7px;
  height: 33%;
  bottom: 50%;
  left: 50%;
}

.clock-hours__arrow {
  width: 12px;
  height: 24%;
  bottom: 50%;
  left: 50%;
}

